import React from 'react'
import { Form, Input, Button, Checkbox, Select, Radio } from 'antd'
import { MainContext } from '../../context/MainContext'
import { useContext } from 'react'
import { v4 as uuidv4 } from 'uuid'
import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'
import { useNavigate } from 'react-router-dom'
import './ContactForm.scss'
function ContactForm({ CurrentConnection }) {
  const { Option } = Select

  const { dispatch } = useContext(MainContext)
  let navigate = useNavigate()
  const [form] = Form.useForm()

  //Əlaqə üzərində düzəliş olan halda aşağıdakı funksiya işləyəcək
  const Editconnection = (value, id) => {
    value.id = id
    if (JSON.stringify(CurrentConnection) !== JSON.stringify(value)) {
      dispatch({ type: 'Remove_Connection', payload: id })
      dispatch({ type: 'Add_Connection', payload: value })
      const resolveAfter3Sec = new Promise((resolve) =>
        setTimeout(resolve, 2000),
      )
      toast.promise(resolveAfter3Sec, {
        pending: 'Dəyişiklər qeydə alınır',
        success: 'Qeydə alındı, Əsas Səhifəyə Yönləndirilirsiniz 👌',
      })
      setTimeout(() => {
        navigate('/Contacts')
      }, 3000)
    } else {
      toast.error('Error! Dəyişiklik yoxdur', {
        position: 'top-center',
        autoClose: 1100,
        closeOnClick: true,
        pauseOnHover: false,
        draggable: true,
        progress: undefined,
      })
    }
  }

  // Əlaqə əlavə olunanda bu funksiya işləyəcək
  const SaveConnection = (value) => {
    value.id = uuidv4()
    dispatch({ type: 'Add_Connection', payload: value })
    const resolveAfter3Sec = new Promise((resolve) => setTimeout(resolve, 2000))
    toast.promise(resolveAfter3Sec, {
      pending: 'Əlaqə əlavə olunur',
      success: 'Əlavə olundu, Əsas Səhifəyə Yönləndirilirsiniz 👌',
    })
    setTimeout(() => {
      navigate('/Contacts')
    }, 3000)
  }

  return (
    <>
      <ToastContainer hideProgressBar position="top-center" />

      <Form
        name="basic"
        form={form}
        labelCol={{
          span: 9,
        }}
        wrapperCol={{
          span: 6,
        }}
        initialValues={
          //Əgər üzərində dəyişiklik ediləcək Əlaqə varsa , əlaqənin ilkin datası fieldlərə yazilir,
          //yoxdursa (yəni ilk dəfə əlavə olunarsa) default olaraq aşağıdakı dəyərlər yazılır
          CurrentConnection?
             {
                firstName: CurrentConnection.firstName,
                lastName: CurrentConnection.lastName,
                fatherName: CurrentConnection.fatherName,
                email: CurrentConnection.email,
                position: CurrentConnection.position,
                Additional_Info: CurrentConnection.Additional_Info,
                gender: CurrentConnection.gender,
                Subscription: CurrentConnection.Subscription,
              }
            : {
                position: 'Vəzifə 1',
                gender: 'Kişi',
                Subscription: false,
              }
        }
        autoComplete="off"
        onFinish={(value) => {
          //Əgər üzərində dəyişiklik ediləcək Əlaqə varsa , Editconnection funksiyasi əlaqə üzərində dəyişiklik edir,
          //yoxdursa SaveConnection funkisyasi onu əlaqələrə əlavə edir
          CurrentConnection?
             Editconnection(value, CurrentConnection.id)
            : SaveConnection(value)

          form.resetFields() // formu sıfırlayır
        }}
      >
        <Form.Item
          label="Ad"
          name="firstName"
          rules={[
            {
              required: true,
              message: 'Zəhmət olmasa adınızı daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Adınızı daxil edin" />
        </Form.Item>

        <Form.Item
          label="Soyad"
          name="lastName"
          rules={[
            {
              required: true,
              message: 'Zəhmət olmasa soyadınızı daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Soyadınızı daxil edin" />
        </Form.Item>

        <Form.Item
          label="Ata adı"
          name="fatherName"
          rules={[
            {
              required: true,
              message: 'Zəhmət olmasa atanızın adını daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Atanızın adını daxil edin" />
        </Form.Item>

        <Form.Item
          label="Email"
          name="email"
          rules={[
            {
              type: 'email',
              message: 'Emailinizi səhvdir!',
            },
            {
              required: true,
              message: 'Zəhmət olmasa emailinizi daxil edin!',
            },
          ]}
          hasFeedback
        >
          <Input placeholder="Emailinizi daxil edin" />
        </Form.Item>

        <Form.Item
          name="Additional_Info"
          label="Əlavə Məlumat"
          rules={[
            {
              required: true,
              message: 'Zəhmət olmasa əlavə məlumat daxil edin!',
            },
          ]}
        >
          <Input.TextArea />
        </Form.Item>

        <Form.Item label="Vəzifə" name="position">
          <Select>
            <Option value="Vəzifə 1">Vəzifə 1</Option>
            <Option value="Vəzifə 2">Vəzifə 2</Option>
            <Option value="Vəzifə 3">Vəzifə 3</Option>
            <Option value="Vəzifə 4">Vəzifə 4</Option>
          </Select>
        </Form.Item>

        <Form.Item label="Cins" name="gender">
          <Radio.Group>
            <Radio value={'Kişi'}>Kişi</Radio>
            <Radio value={'Qadın'}>Qadın</Radio>
            <Radio value={'Digər'}>Digər</Radio>
          </Radio.Group>
        </Form.Item>

        <Form.Item
          name="Subscription"
          valuePropName="checked"
          wrapperCol={{
            md: {
              offset: 9,
              span: 16,
            },
          }}
        >
          <Checkbox>Yeniliklər barədə məlumat bağlı məlumat ver </Checkbox>
        </Form.Item>

        <Form.Item
          wrapperCol={{
            md: {
              offset: 9,
              span: 16,
            },
          }}
        >
          <Button className="Addbtn" htmlType="submit">
            Təsdiqlə
          </Button>
        </Form.Item>
      </Form>
    </>
  )
}

export default ContactForm
